<template>
  <b-card :header="$t('Users')">
    <!-- <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" @click="$refs.addUserModel.show()" class="mr-1 mb-2 mt-xl-n2 pull-right btn-color">{{$t('Add_Users')}}</b-button> -->

    <b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="gridData" :fields="captions" :current-page="currentPage" :per-page="perPage">
     <template slot="top-row" slot-scope="{ fields }">
        <td v-for="field in fields" :key="field.key"  >
          <input   v-model="filters[field.key]" :placeholder="' '+field.label"  class="form-control" v-if="field.key!='actions' && field.key!='isActive' " >
        </td> 
      </template>
    
    <template slot="actions" slot-scope="data">
        <!-- <a v-on:click="selectObject(data.item), $refs.editUserModel.show()" v-b-tooltip.hover :title="$t('Update')" class="icon mr-2 anchor-pointer"><i id="editIcon"  class="fa font-2x2 fa-pencil"></i></a> -->
        <a v-on:click="selectObject(data.item),$refs.disableUserModel.show()" v-b-tooltip.hover :title="$t('enable_User')"  class="icon mr-2 anchor-pointer" v-if="data.item.isActive=='Inactive'"><i class="fa fa-check-circle font-2x2 icons mt-1"></i></a>
        <a v-on:click="selectObject(data.item),$refs.disableUserModel.show()" v-b-tooltip.hover :title="$t('Disable_User')"  class="icon mr-2 anchor-pointer" v-if="data.item.isActive=='Active'"><i class="fa fa-ban font-2x2 icons mt-1"></i></a>
        <a v-on:click="selectObject(data.item), $refs.deleteUserModel.show()" v-b-tooltip.hover :title="$t('Delete')"  class="icon anchor-pointer" ><i class="cui-trash font-2x2 icons mt-1"></i></a>
    </template>
    </b-table>
    <div class="pt-1 pb-4" v-if="gridData2 && gridData2.length == 0">
      <!-- <b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button> -->
           <div class="text-center">
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
          </div>
    </div>
    <nav>
      <b-pagination class="pull-right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
    </nav>
    <b-modal no-close-on-backdrop ref="disableUserModel" :title="$t('EnableDisable_of_User')" class="modal-primary">
      <p>{{$t('Are_you_sure_you_want_to_Change_Status_of')+ disableUserProfile.FirstName+" "+disableUserProfile.LastName}} <b style="color: #ef402b"></b> ?</p>
      <div slot="modal-footer" class="w-100">
        <b-button type="button" v-on:click="$refs.disableUserModel.hide()" class="float-right ml-2 "><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" v-on:click=" disableUser($refs.disableUserModel,disableUserProfile)" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('Confirm')}}</b-button>
      </div>
    </b-modal>
    <b-modal no-close-on-backdrop ref="deleteUserModel" :title="$t('Delete_User')" class="modal-primary">
      <p>{{$t('Are_you_sure_you_want_to_delete') +disableUserProfile.FirstName+" "+disableUserProfile.LastName}} <b style="color: #ef402b"></b> ?</p>
      <div slot="modal-footer" class="w-100">
        <b-button type="button" v-on:click="$refs.deleteUserModel.hide()" class="float-right ml-2 "><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" v-on:click="deleteItem($refs.deleteUserModel)" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('Confirm')}}</b-button>
      </div>
    </b-modal>
  </b-card>
</template>

<script>
import VueNotifications from 'vue-notifications'
import * as toutPaieService from "./ToutPaieService.js";

export default {
  props: {
    caption: {
      type: String,
      default: 'Managers'
    },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    }
  },
  data: () => {
    return {
      selectedUser : {
        username : null,
        firstName : null,
        lastName : null,
        username : null,
        userProfile : null,
        store : null
      },
      disableUserProfile : {
        username : null,
        firstName : null,
        lastName : null,
        username : null,
        userProfile : null,
        store : null
      },
      disable:false,
      deleting:false,
      sortBy : 'FirstName',
      currentPage: 1,
      file: null,
      perPage:20,
      usersList : [],
      filters: {
                FirstName: '',
                LastName: ''
              },
      countriesList : [],
      storesList:[],
      store: [
        'store 1',
        'store 2',
        'store 3'
      ]
    }
  },
  computed: {
    fields () {
      return [
        {key: 'FirstName',label: this.translateString('First_Name'),  sortable: true},
        {key: 'LastName',label: this.translateString('Last_Name'),  sortable: true},
        {key: 'Email', label: this.translateString('Email'), sortable: true},
        {key: 'MobileNumber',label: this.translateString('Mobile_Number'),sortable: true},
        {key: 'isActive',label: this.translateString('Status'),sortable: true},
        {key: 'actions' ,label: this.translateString('Action'),sortable:false,thStyle: {'width': '70px !important', 'min-width': '70px !important'}}
      ]
    },
    gridData2: function() {
      return this.usersList;
    },

     gridData () {
      const filtered = this.usersList.filter(item => {
        return Object.keys(this.filters).every(key =>
            // String(item[key]).includes(this.filters[key]))
            String(item[key]).toLowerCase().includes( String(this.filters[key]).toLowerCase()))
      })
      return filtered.length > 0 ? filtered : [{
        FirstName: '',
      LastName: ''
      }]
    },
    totalRows: function () {
      return this.getRowCount()
    },
    captions: function() {
      return this.fields
    }
  },
  methods: {
    translateString : function(colName) {
      return this.$i18n.t(colName);
    },
    getRowCount: function () {
       return this.usersList.length;
    },

    disableStore: function(data) {
      data.status = 'Disabled';
    },
     clearObjects: function() {
       this.selectedUser={};
       this.disableUserProfile={};
    },

    selectObject: function(user){
      
      
        this.disableUserProfile = user;
 
         this.selectedUser = user;
       

    },

    deleteItem: function (ref) {
      let URL = 'user/delete/' + this.selectedUser.UserID;

      // toutPaieService.deleteData(this.selectedUser, URL).then(data => {
      //   if (data.status == 200) {
      //     ref.hide();
      //     this.getUsers();
      //   }
      // });

    toutPaieService.getData(URL).then(res => {
       let msg=res.status;
       if (msg=="Success") {
          
          ref.hide();
          VueNotifications.success({message:this.translateString("User_Deleted_Successfully"),timeout:2500});
          this.clearObjects();
          this.getUsers();
          
        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }

    });

    },

    disableUser: function(ref,selectedUser) {
      
      let url =null; 

      if(selectedUser.isActive=="Y" || selectedUser.isActive=="Active"){
      url='user/deactivate/' + selectedUser.UserID;
      }else{
        url='user/activate/' + selectedUser.UserID;
      }

    toutPaieService.saveData(selectedUser, url).then(data => {
        // console.log('data', data);
        let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
          ref.hide();
          this.getUsers();
          if(selectedUser.isActive=="Y" || selectedUser.isActive=="Active"){
          VueNotifications.success({message:this.translateString("User_Disabled_Successfully_Saved"),timeout:2500});
          }else{
             VueNotifications.success({message:this.translateString("User_Enabled_Successfully_Saved"),timeout:2500});
          }
          this.clearObjects();

        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }
      });

    },

    getUsers: function () {
      let url = 'users';
      toutPaieService.getData(url).then(res => {
        this.usersList = res.data;

          for(var i=0;i<this.usersList.length;i++)
          {
            this.usersList[i].isActive=(this.usersList[i].isActive=="Y"?"Active":"Inactive");
          }


      });
      // console.log(this.usersList);
    }
  },

  beforeMount(){
    this.getUsers();
  }
}
</script>
